import React, { useState } from 'react'
import { useStaticQuery, graphql,  Link } from 'gatsby'
import { Nav, Tab } from 'react-bootstrap'
import BreadcrumContent from '../../../../Global/Pages/BreadcrumbContent.component'
import PropertyListItem from '../../../../Global/Pages/Listing/PropertyListItem.component'
import Review from '../../../../Global/Pages/Review.component'
import AgentListItem from '../../../../Global/Pages/Listing/AgentListItem.component'
import SidebarFeatured from '../../../../Global/Pages/SideBarFeaturedListing.component'
import GridFeaturedProperties from '../../../../Global/Pages/Listing/Grid/GridFeaturedProperties.component'
import CategoriesProperty from '../../../../Global/Pages/CategoriesProperty.component'
import ContactAgencyAgent from '../../../../Global/Pages/Listing/ContactAgencyAgent.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
const AgentSingleSection=() =>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            
            agent: file(relativePath: {eq: "team/11.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
    return (
        <section className="our-agent-single bgc-f7 pb30-991">
		<div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <div className="listing_sidebar dn db-991">
                        <div className="sidebar_content_details style3">
                            <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                <div className="sidebar_advanced_search_widget">
                                    <h4 className="mb25">
                                    Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                    </Link>
                                    </h4>
                                    <AdvanceSearchWidgetSidebar style2/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            <div className="row">
				<div className="col-md-12 col-lg-8">
                    <div className="row">
						<div className="col-lg-12">
                            <BreadcrumContent title="Agent Single" active="Agent Single"/>
                            <div className="dn db-991 mt30 mb0">
								<div id="main2">
									<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
								</div>
							</div>
                        </div>
                        <div className="col-lg-12">
                            <AgentListItem
                                    name="Christopher Pakulla"
                                    office="134 456 3210"
                                    mobile="891 456 9874"
                                    fax="342 654 1258"
                                    email="agencies@findhouse.com"
                                    facebook="#"
                                    twitter="#"
                                    instagram="#"
                                    pinterest="#"
                                    dribbble="#"
                                    google="#"
                                    Havelisting
                                    ListView
                                >
                                    <img className="img-fluid" src={data.agent.childImageSharp.fluid.originalImg} alt="3.jpg"/>
                                </AgentListItem>
                                <div className="shop_single_tab_content style2 mt30">
                                <Tab.Container  defaultActiveKey="description">
                                    <Nav as='ul' className="nav nav-tabs">
                                        <Nav.Item as="li" className="nav-item">
                                            <Nav.Link className="nav-link"eventKey="description">Description</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item as="li" className="nav-item">
                                            <Nav.Link className="nav-link"eventKey="listing">Listing</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item as="li" className="nav-item">
                                            <Nav.Link className="nav-link" eventKey="review">Reviews</Nav.Link>
                                        </Nav.Item>
                                    </Nav>
                                    <Tab.Content>
                                        <Tab.Pane eventKey="description" className="fade">
                                            <div className="product_single_content">
                                                <div className="mbp_pagination_comments">
                                                    <div className="mbp_first media">
                                                        <div className="media-body">
                                                            <p className="mb25">Evans Tower very high demand corner junior one bedroom plus a large balcony boasting full open NYC views. You need to see the views to believe them. Mint condition with new hardwood floors. Lots of closets plus washer and dryer.</p>
                                                            <p className="mt10 mb0">Fully furnished. Elegantly appointed condominium unit situated on premier location. PS6. The wide entry hall leads to a large living room with dining area. This expansive 2 bedroom and 2 renovated marble bathroom apartment has great windows. Despite the interior views, the apartments Southern and Eastern exposures allow for lovely natural light to fill every room. The master suite is surrounded by handcrafted milkwork and features incredible walk-in closet and storage space.</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </Tab.Pane>
                                        <Tab.Pane eventKey="listing">
                                            <div className="col-lg-12">
                                                <PropertyListItem listfor='For Rent'
                                                featured
                                                price="13000"
                                                category="Appartment"
                                                title="Renovated Apartment"
                                                addClass="style2 hvr-bxshd bdrrn mb10 mt20"
                                                >
                                                    <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                                </PropertyListItem>
                                            </div>
                                            <div className="col-lg-12">
                                            <PropertyListItem 
                                                featured
                                                listfor='For Rent'
                                                price="13000"category="Villa"
                                                title="Gorgeous Villa Bay View"
                                                addClass="style2 hvr-bxshd bdrrn mb10 mt20"
                                                >
                                                    <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                                </PropertyListItem>
                                            </div>
                                            <div className="col-lg-12">
                                                <PropertyListItem listfor='For Sale'
                                                    price="13000"category="Single Family Home"
                                                    title="Luxury Family Home"
                                                    addClass="style2 hvr-bxshd bdrrn mb10 mt20"
                                                    featured
                                                    >
                                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                                </PropertyListItem>
                                            </div>
                                        </Tab.Pane>
                                        <Tab.Pane eventKey="review" className="review">
                                            <Review/>
                                        </Tab.Pane>
                                    </Tab.Content>
					            </Tab.Container>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-xl-4">
                    <div className="sidebar_listing_grid1 dn-991">
						<div className="sidebar_listing_list">
							<div className="sidebar_advanced_search_widget">
                                <h4 className="mb25">Contact Christopher Pakulla</h4>
                                <ContactAgencyAgent/>
                            </div>
                        </div>
                        <GridFeaturedProperties/>
                        <CategoriesProperty/>
                        <SidebarFeatured HeaderTitle="Recently Viewed"/>
                    </div>
                </div>
            </div>
        </div>
        </section>
    )
}

export default AgentSingleSection
